<template>
  <van-list
    class="shopcart"
    v-model="loadingMsg.isLoading"
    :finished="loadingMsg.isFinished"
    :finished-text="loadingMsg.msg"
    @load="onload"
    :offset="50"
  >
    <div v-if="shopcartData.length == 0">
      <van-empty description="这里啥都没有哦" />
    </div>
    <div class="contentBox" v-else>
      <van-nav-bar
        title="购物车"
        :right-text="isManage ? '管理':'完成'"
        left-text="返回"
        fixed
        @click-left="goBack"
        @click-right="toggleFn"
      />

      <van-swipe-cell
        class="content clearFix"
        v-for="(item,index) in shopcartData"
        :key="index"
        right-width="500px"
      >
        <div class="box">
          <div class="left fl">
            <van-checkbox
              class="checked fl"
              v-model="item.isCheck"
              @change="selectFn"
              checked-color="slateblue"
            ></van-checkbox>
          </div>
          <div class="imgBox fl">
            <img class="auto-img fl smallImg" :src="item.small_img" alt="图片未加载..." />
          </div>
          <div class="right fl">
            <div class="up clearFix">
              <span class="fl name">{{item.name}}</span>
              <span class="fl desc">{{item.rule}}</span>
            </div>
            <div class="center">
              <span class="enname">{{item.enname}}</span>
            </div>
            <div class="down clearFix">
              <span class="price fl">￥{{item.price}}</span>
              <van-stepper
                class="fr"
                v-model="item.count"
                theme="round"
                button-size="22"
                disable-input
                @change="changeCount(item)"
              />
            </div>
          </div>
        </div>
        <template #right>
          <van-button @click="remove([item.sid],index)" type="danger" text="删除" />
        </template>
      </van-swipe-cell>
    </div>
    <div v-if="isManage">
      <van-submit-bar
        class="foot"
        :price="total"
        button-text="提交订单"
        :disabled="isNotChecked"
        @submit="goSubmit"
      >
        <van-checkbox v-model="isAllChecked" checked-color="slateblue" @click="allSelectFn">全选</van-checkbox>
      </van-submit-bar>
    </div>
    <div v-else>
      <van-submit-bar class="foot" button-text="删除选中" :disabled="isNotChecked" @submit="removeAll">
        <van-checkbox v-model="isAllChecked" checked-color="slateblue" @click="allSelectFn">全选</van-checkbox>
      </van-submit-bar>
    </div>
  </van-list>
</template>

<script>
import "../../assets/css/shopcart.less";
export default {
  data() {
    return {
      // 全选 是否全选
      isAllChecked: false,
      // 是否有商品被选中
      isManage: true,
      // 单选 是否未选中
      isNotChecked: true,
      // 默认显示的购物车数据
      shopcartData: [],
      // 总购物车数据
      allShopcartData: [],
      // 是否切换文字
      isToggleText: true,
      // 加载数据
      loadingMsg: {
        isLoading: false,
        isFinished: false,
        msg: "下面啥也没有啦"
      },
      // 默认加载数据数目
      loadCount: 8,
      // 总价
      total: 0
    };
  },
  created() {
    this.getProductData();
  },
  methods: {
    // 获取数据
    getProductData() {
      let tkStr = localStorage.getItem("__Tk");
      // 
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 5000) {
            result.data.result.forEach(val => {
              val.isCheck = false;
            });
            this.allShopcartData = result.data.result;
            this.shopcartData = this.allShopcartData.splice(0, this.loadCount);
            // 
            if (this.allShopcartData.length > 0) {
              this.loadingMsg.isLoading = false;
            } else {
              this.loadingMsg.isLoading = true;
              this.loadingMsg.isFinished = true;
            }
            if (this.shopcartData.length == 0) {
              this.loadingMsg.msg = "";
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 返回上一页
    goBack() {
      this.$router.go(-1);
    },
    // 加载数据
    onload() {
      // 
      setTimeout(() => {
        this.shopcartData.push(
          ...this.allShopcartData.splice(0, this.loadCount)
        );
        if (this.allShopcartData.length == 0) {
          this.loadingMsg.isLoading = true;
          this.loadingMsg.isFinished = true;
        }
      }, 1000);
    },
    // 全选
    allSelectFn() {
      this.sum();
      // 
      this.allShopcartData.forEach(val => {
        val.isCheck = this.isAllChecked;
      });
      this.shopcartData.forEach(val => {
        val.isCheck = this.isAllChecked;
      });
      this.isNotChecked = !this.isAllChecked;
    },
    // 单选
    selectFn() {
      this.sum();
      this.isNotChecked = true;
      for (let i = 0; i < this.shopcartData.length; i++) {
        if (this.shopcartData[i].isCheck) {
          this.isNotChecked = false;
          break;
        }
      }
      for (let i = 0; i < this.shopcartData.length; i++) {
        if (!this.shopcartData[i].isCheck) {
          this.isAllChecked = false;
          return;
        }
      }
      this.isAllChecked = true;
    },
    // 删除/管理切换
    toggleFn() {
      this.isManage = !this.isManage;
    },
    // 修改购物车数据
    changeCount(item) {
      // 
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          sid: item.sid,
          count: item.count
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 6000) {
            this.sum();
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 删除单个商品
    remove(sids, index) {
      
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            this.shopcartData.splice(index, 1);
            if (this.shopcartData.length == 0) {
              this.isHas = true;
              this.loadingMsg.msg = "";
            }
            this.sum();
            this.$toast.success("删除成功");
          } else {
            this.$toast.fail("删除成功");
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 删除全部
    removeAll() {
      let sids = [];
      this.shopcartData.forEach(val => {
        if (val.isCheck) {
          sids.push(val.sid);
        }
      });
      // 
      let tkStr = localStorage.getItem("__Tk");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 7000) {
            for (let i = this.shopcartData.length - 1; i >= 0; i--) {
              if (this.shopcartData[i].isCheck) {
                this.shopcartData.splice(i, 1);
              }
            }
            this.sum();
            if (this.shopcartData.length == 0) {
              this.isAllChecked = false;
              this.loadingMsg.msg = "";
            }
            this.$toast.success("删除成功");
          } else {
            this.$toast.fail("删除失败");
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    // 计算金额
    sum() {
      this.total = 0;
      this.shopcartData.forEach(val => {
        if (val.isCheck) {
          this.total += val.count * val.price * 100;
        }
      });
    },
    // 提交订单
    goSubmit() {
      let sids = [];
      this.shopcartData.forEach(val => {
        if (val.isCheck) {
          sids.push(val.sid);
        }
      });
      
      this.$router.push({ name: "Submit", query: { sids: sids.join("-") } });
    }
  }
};
</script>

<style lang="less" scoped>
</style>